import React, { useState } from 'react';
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  sendPasswordResetEmail,
  signInWithPopup,
  GoogleAuthProvider,
  OAuthProvider
} from 'firebase/auth';
import { auth } from './firebase'; //      Firebase
import { useNavigate } from 'react-router-dom';
import './Login.css';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [isRegistering, setIsRegistering] = useState(false);
  const [isResettingPassword, setIsResettingPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  const googleProvider = new GoogleAuthProvider(); //  Google
  const appleProvider = new OAuthProvider('apple.com'); //  Apple

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      if (isRegistering) {
        await createUserWithEmailAndPassword(auth, email, password);
        navigate('/tracks');
      } else {
        await signInWithEmailAndPassword(auth, email, password);
        navigate('/tracks');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleGoogleSignIn = async () => {
    try {
      setIsLoading(true);
      await signInWithPopup(auth, googleProvider);
      navigate('/tracks');
    } catch (err) {
      setError('    Google');
    } finally {
      setIsLoading(false);
    }
  };

  const handleAppleSignIn = async () => {
    try {
      setIsLoading(true);
      const result = await signInWithPopup(auth, appleProvider);
      const credential = OAuthProvider.credentialFromResult(result);
      const token = credential?.idToken;
      console.log('Apple Token:', token);
      navigate('/tracks');
    } catch (err) {
      setError('    Apple');
    } finally {
      setIsLoading(false);
    }
  };

  const handleResetPassword = async () => {
    setIsLoading(true);
    try {
      await sendPasswordResetEmail(auth, email);
      alert('    !');
    } catch (err) {
      setError('      ');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="login-container">
      {isLoading && (
        <p style={{ color: '#888', textAlign: 'center', fontSize: '0.9rem', marginBottom: '15px' }}>
                 
        </p>
      )}

      <h2>{isRegistering ? '' : ''}</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label>Email</label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div>
          <label></label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button type="submit" disabled={isLoading}>
          {isRegistering ? '' : ''}
        </button>
      </form>

      {error && <p style={{ color: 'red' }}>{error}</p>}

      <div>
        {isRegistering ? (
          <p>
              ?{' '}
            <span onClick={() => setIsRegistering(false)} style={{ cursor: 'pointer' }}>
              
            </span>
          </p>
        ) : (
          <p>
             ?{' '}
            <span onClick={() => setIsRegistering(true)} style={{ cursor: 'pointer' }}>
              
            </span>
          </p>
        )}
      </div>

      {!isRegistering && (
        <div>
          <span onClick={handleResetPassword} style={{ cursor: 'pointer', color: '#1db954' }}>
             ?
          </span>
        </div>
      )}

      {!isRegistering && (
        <div>
          <button className="google-signin-button" onClick={handleGoogleSignIn} disabled={isLoading}>
              Google
          </button>
          <button className="apple-signin-button" onClick={handleAppleSignIn} disabled={isLoading}>
              Apple
          </button>
        </div>
      )}
    </div>
  );
}

export default Login;
