import React from 'react';

function PlayPauseButton({ isPlaying, onPlayPauseClick }) {
  return (
    <button onClick={onPlayPauseClick} style={{ background: 'none', border: 'none', cursor: 'pointer' }}>
      {isPlaying ? (
        <img src="/svg/pause.svg" alt="Pause" width={30} />
      ) : (
        <img src="/svg/play.svg" alt="Play" width={30} />
      )}
    </button>
  );
}

export default PlayPauseButton;
