import React, { useEffect, useRef, useState } from 'react';
import axios from 'axios';
import './firebase';
import { getAuth, onAuthStateChanged } from 'firebase/auth';
import { getDatabase, ref, set, remove, onValue } from 'firebase/database';
import PlayPauseButton from './PlayPauseButton';
import VolumeSlider from './VolumeSlider';
import './player.css';
import LoopIcon from './loop.svg';
import LikeIcon from './like.svg';
import LikedIcon from './liked.svg';
import SearchIcon from './search.svg';

const RPC_SERVER_URL = 'http://academic-lectures.gl.at.ply.gg:61934/update-rpc';

function Tracks() {
  const [tracks, setTracks] = useState([]);
  const [favorites, setFavorites] = useState({});
  const [selectedTrack, setSelectedTrack] = useState(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isLooping, setIsLooping] = useState(false);
  const [volume, setVolume] = useState(0.5);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [lyrics, setLyrics] = useState([]);
  const [currentLyricIndex, setCurrentLyricIndex] = useState(null);
  const [user, setUser] = useState(null);
  const [searchTerm, setSearchTerm] = useState(''); //   

  const audioRef = useRef(null);

  useEffect(() => {
    const auth = getAuth();
    onAuthStateChanged(auth, (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        fetchFavorites(currentUser.email);
      } else {
        setUser(null);
        setFavorites({});
      }
    });

    fetchTracks();
  }, []);

  const fetchTracks = async () => {
    try {
      const response = await axios.get('https://a1055353.xsph.ru:5000/tracks');
      setTracks(response.data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  const filteredTracks = tracks.filter((track) =>
    track.name.toLowerCase().includes(searchTerm.toLowerCase())
  );  

  const updateRPC = async (track, isPlaying) => {
    try {
      if (isPlaying) {
        await axios.post(RPC_SERVER_URL, {
          trackName: track.name,
          artistName: track.artist || ' ',
          duration: duration,
        });
        console.log('Rich Presence .');
      } else {
        console.log(' , RPC  .');
      }
    } catch (error) {
      console.error('  RPC:', error.response ? error.response.data : error.message);
    }
  };

  const handleTrackSelection = async (track) => {
    setSelectedTrack(track);
    setIsPlaying(true);
    if (audioRef.current) {
      audioRef.current.src = `https://a1055353.xsph.ru:5000/tracks/${track.file}`;
      audioRef.current.play();
    }

    try {
      const lrcResponse = await axios.get(`${track.file}.lrc`);
      const parsedLyrics = parseLRC(lrcResponse.data);
      setLyrics(parsedLyrics);
    } catch (error) {
      console.error('   :', error);
      setLyrics([]);
    }

    updateRPC(track, true);
  };

  const togglePlayPause = () => {
    if (audioRef.current) {
      if (isPlaying) {
        audioRef.current.pause();
      } else {
        audioRef.current.play();
      }
      setIsPlaying(!isPlaying);
      updateRPC(selectedTrack, !isPlaying);
    }
  };

  const toggleLoop = () => {
    const newLoopState = !isLooping;
    setIsLooping(newLoopState);
    if (audioRef.current) {
      audioRef.current.loop = newLoopState;
    }
  };

  const handleVolumeChange = (newVolume) => {
    setVolume(newVolume);
    if (audioRef.current) {
      audioRef.current.volume = newVolume;
    }
  };

  const handleTimeUpdate = () => {
    const current = audioRef.current.currentTime;
    setCurrentTime(current);
    setDuration(audioRef.current.duration);

    const currentIndex = lyrics.findIndex((lyric, index) => {
      const nextLyric = lyrics[index + 1];
      return current >= lyric.time && (!nextLyric || current < nextLyric.time);
    });

    setCurrentLyricIndex(currentIndex);
  };

  const parseLRC = (lrcText) => {
    return lrcText
      .split('\n')
      .map((line) => {
        const match = line.match(/\[(\d+):(\d+\.\d+)\](.*)/);
        if (match) {
          const minutes = parseInt(match[1], 10);
          const seconds = parseFloat(match[2]);
          return { time: minutes * 60 + seconds, text: match[3].trim() };
        }
        return null;
      })
      .filter((line) => line);
  };

  const sanitizeEmail = (email) => {
    return email.replace(/[.#$\[\]]/g, '_');
  };

  const toggleFavorite = (track) => {
    if (!user) {
      console.error('   ,     .');
      return;
    }

    const db = getDatabase();
    const sanitizedEmail = sanitizeEmail(user.email);
    const trackRef = ref(db, `favorites/${sanitizedEmail}/${track.name}`);

    if (favorites[track.name]) {
      remove(trackRef);
    } else {
      set(trackRef, true);
    }
  };

  const fetchFavorites = (email) => {
    const db = getDatabase();
    const sanitizedEmail = sanitizeEmail(email);
    const favoritesRef = ref(db, `favorites/${sanitizedEmail}`);
    onValue(favoritesRef, (snapshot) => {
      const data = snapshot.val() || {};
      setFavorites(data);
    });
  };

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  return (
    <div style={{ display: 'flex', alignItems: 'flex-start' }}>
      <div style={{ flex: 1 }}>
      <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px', width: '100%' }}>
        <img
          src={SearchIcon}
          alt="Search"
          style={{ width: '24px', height: '24px', marginRight: '8px' }}
        />
        <input
          type="text"
          placeholder=" ..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          style={{
            flex: 1,
            padding: '10px',
            borderRadius: '4px',
            border: '1px solid #ccc',
            outline: 'none',
          }}
        />
      </div>
      {searchTerm && (
        <div style={{ marginBottom: '20px', width: '100%' }}>
          <h3 style={{ fontSize: '16px', margin: '10px 0', color: '#FFF' }}> :</h3>
          <ul style={{ listStyleType: 'none', padding: 0 }}>
            {filteredTracks.length > 0 ? (
              filteredTracks.map((track, index) => (
                <li key={`search-${index}`} style={{ marginBottom: '10px' }}>
                  <button
                  onClick={() => handleTrackSelection(track)}
                    style={{
                      backgroundColor: '#333',
                      color: '#fff',
                      padding: '10px 15px',
                      border: 'none',
                      borderRadius: '4px',
                      cursor: 'pointer',
                      width: '100%',
                      textAlign: 'left',
                    }}
                  >
                    {track.name}
                  </button>
                </li>
              ))
            ) : (
              <p style={{ color: '#999' }}> </p>
            )}
          </ul>
        </div>
      )}
        <h2></h2>
        <ul style={{ listStyleType: 'none', padding: 0 }}>
          {tracks
            .filter((track) => favorites[track.name])
            .map((track, index) => (
              <li key={`fav-${index}`} style={{ marginBottom: '10px' }}>
                <button
                  onClick={() => handleTrackSelection(track)}
                  style={{
                    backgroundColor: '#333',
                    color: '#fff',
                    padding: '10px 15px',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    width: '100%',
                    textAlign: 'left',
                  }}
                >
                  {track.name}
                </button>
              </li>
            ))}
        </ul>

        <div style={{ flex: 1, width: '100%' }}>
        <h2> </h2>
        <ul style={{ listStyleType: 'none', padding: 0 }}>
          {tracks.map((track, index) => (
            <li key={index} style={{ marginBottom: '10px' }}>
              <button
                  onClick={() => handleTrackSelection(track)}
                style={{
                  backgroundColor: '#333',
                  color: '#fff',
                  padding: '10px 15px',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  width: '100%',
                  textAlign: 'left',
                }}
              >
                {track.name}
              </button>
            </li>
          ))}
        </ul>
      </div>
    </div>

      <div style={{ flex: 1, marginLeft: '20px' }}>
        <h2></h2>
        {selectedTrack ? (
          <div>
            <p> : {selectedTrack.name}</p>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <PlayPauseButton isPlaying={isPlaying} onPlayPauseClick={togglePlayPause} />
              <button onClick={toggleLoop} style={{ background: 'none', border: 'none', cursor: 'pointer' }}>
                <img
                  src={LoopIcon}
                  alt="Loop"
                  style={{ width: '24px', height: '24px', filter: isLooping ? 'brightness(1.2)' : 'brightness(0.8)' }}
                />
              </button>
              <button onClick={() => toggleFavorite(selectedTrack)} style={{ background: 'none', border: 'none', cursor: 'pointer' }}>
                <img
                  src={favorites[selectedTrack?.name] ? LikedIcon : LikeIcon}
                  alt="Like"
                  style={{ width: '24px', height: '24px' }}
                />
              </button>
              <VolumeSlider volume={volume} onVolumeChange={handleVolumeChange} />
            </div>
            <div style={{ marginTop: '10px' }}>
              <input
                type="range"
                min="0"
                max="100"
                value={duration ? (currentTime / duration) * 100 : 0}
                onChange={(e) => (audioRef.current.currentTime = (e.target.value / 100) * duration)}
                style={{ width: '100%' }}
              />
              <div style={{ display: 'flex', justifyContent: 'space-between', fontSize: '14px', color: '#555' }}>
                <span>{formatTime(currentTime)}</span>
                <span>{formatTime(duration)}</span>
              </div>
            </div>

            <div style={{ marginTop: '10px', fontSize: '16px', color: '#333' }}>
              {lyrics.map((lyric, index) => (
                <p
                  key={index}
                  style={{
                    fontSize: index === currentLyricIndex ? '20px' : '16px',
                    color: index === currentLyricIndex ? '#FFF' : '#555',
                    fontWeight: index === currentLyricIndex ? 'bold' : 'normal',
                    margin: 0,
                    transition: 'all 0.3s ease',
                  }}
                >
                  {lyric.text}
                </p>
              ))}
            </div>

            <audio
              ref={audioRef}
              onTimeUpdate={handleTimeUpdate}
              onPlay={() => setIsPlaying(true)}
              onPause={() => setIsPlaying(false)}
              onEnded={() => setIsPlaying(false)}
            />
          </div>
        ) : (
          <p>   </p>
        )}
      </div>
    </div>
  );
}

export default Tracks;

